@import '../../../config.css'; 



.notVisible {
    position: fixed;
    right: 0%;
    bottom: 0%;
    z-index: 2;
    height: 50px;
    width: 50px;
    border-radius: 50%;
    color: black;
    margin: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: 0.8em;
    opacity: 0;

    
    -webkit-transition: border-radius 0.3s ease-in-out, 
                background-color 0.2s ease-in-out, 
                bottom 0.2s ease-in-out, 
                right 0.2s ease-in-out, 
                height 0.3s ease-in-out,
                width 0.3s ease-in-out,
                opacity 0.2s ease-in-out;

    
    -o-transition: border-radius 0.3s ease-in-out, 
                background-color 0.2s ease-in-out, 
                bottom 0.2s ease-in-out, 
                right 0.2s ease-in-out, 
                height 0.3s ease-in-out,
                width 0.3s ease-in-out,
                opacity 0.2s ease-in-out;

    
    transition: border-radius 0.3s ease-in-out, 
                background-color 0.2s ease-in-out, 
                bottom 0.2s ease-in-out, 
                right 0.2s ease-in-out, 
                height 0.3s ease-in-out,
                width 0.3s ease-in-out,
                opacity 0.2s ease-in-out;
}

.visible {
 
    width: 95%;
    height: 90%;
    border-radius: 0px; 
    bottom: 50%;
    right: 50%;
    margin: 0 auto;
    background-color: var(--offWhite);
    z-index: 2;
    -webkit-transform: translate(50%, 50%);
        -ms-transform: translate(50%, 50%);
            transform: translate(50%, 50%);
    opacity: 1;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto auto auto auto 60vh 60vh;
    font-size: 1rem;
    color: grey;
}

.button {
    grid-column: 1/6;    
    width: 15%;
    height: 5%;

}

.heading {
    grid-column: 1/6;
    font-family: var(--handwritingFont);
    font-size: 2em;
    margin: 0px;
    color: var(--goldFont);
    text-align: center;
}

.subHeading {
    grid-column: 1/6;
    font-family: var(--mainFont);
    font-size: 1.2em;
    color: grey;
    margin: 15px 0px 0px 5px;
    text-align: center;
}

.formOutline{
    grid-column: 1/6;
    text-align: center;
    width: 100%;
    font-family: var(--mainFont);
}

.formElements > * {
    margin: 0px 3px 0px 3px;
}

.name {
    padding: 15px 0px 5px 0px;
}

.input {
    display: inline-block;
}

.disclaimer {
    grid-column: 1/6;
    text-align: center;
    font-style: italic;
}

.warning {
    color: red;
}



@media (min-width: 768px) {
    .notVisible {
        -webkit-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    } 

} 


